import {
	RECORD_USERINFO,
	ADD_ADDRESS,
	BUY_CART,
	RECORD_DETAIL_USERINFO,
	RECORD_JOB,
} from './mutation-types.js'

export default {
	// 记录当前经度纬度
	[RECORD_USERINFO](state, {
		userId,
		session,
		name
	}) {
		state.userId = userId;
		state.session = session;
		state.name = name;
	},
	// 记录当前经度纬度
	[RECORD_DETAIL_USERINFO](state, {
		user
	}) {
		state.detailUser = user
		console.log(user)
	},
	// 记录当前经度纬度
	[RECORD_JOB](state, {
		job
	}) {
		state.job = job
		console.log(job)
	},

	
	//增加地址
	[ADD_ADDRESS](state, obj) {
		state.removeAddress = [obj, ...state.removeAddress];
	},
	//会员卡价格纪录
	[BUY_CART](state, price) {
		state.cartPrice = price;
	},

}
