export const getReport = (month) =>{
	var query = new Parse.Query(Report);
	query.equalTo("parent", Parse.User.current());
	if(!month){
		month = getMonthTime()
	}
	query.equalTo("month", month);
	return query
}
export const getRevenue = (month) =>{
	var query = new Parse.Query(Revenue);
	query.equalTo("parent", Parse.User.current());
	if(!month){
		month = getMonthTime()
	}
	query.equalTo("month", month);
	return query
}

