export const getCardQuery = (dateString)=>{
	var query = new Parse.Query(Record);
	query.equalTo("parent", Parse.User.current());
	query.limit(2);
	// let curDate = new Date();
	
	//当天12点
	console.log(dateString)
	// dateString = "2019-05-22"
	var curDate = new Date(new Date(new Date().toLocaleDateString()).getTime());
	// var curDate = new Date(new Date(new Date(day[0],day[1],day[2]).toLocaleDateString()).getTime());
	if(dateString){
		curDate = new Date(dateString);
	}
	// var preDate = new Date(curDate.getTime() - 24*60*60*1000);
	var endCurDate = new Date(curDate.getTime() + 1000 * 60 * 60 * 24)
	query.greaterThan("createdAt", curDate);
	query.lessThan("createdAt", endCurDate);
	return query
}



export const updateIncome = ()=>{
	var cal = {
		// "parent":Parse.User.current(),
		"uptimes":0,
		"downtimes":0,
		"uphours":0,
		"month":"",
		"calIncome":""
	}
  
	const query = new Parse.Query("Record");
	query.greaterThan("createdAt", getMonthStartDate());
	query.ascending("time");
	query.equalTo("parent", Parse.User.current());
	let uptimes = []
	let listByDay = {}

	query.find().then(
		function(results) {
			// each of results will only have the selected fields available.
			for (let i = 0; i < results.length; ++i) {
				let record = results[i]
				if(record.get('action')){
					uptimes.push(record)
				}
					if(!listByDay[record.get('day')]){
						listByDay[record.get('day')] = []
					}
					listByDay[record.get('day')].push(record)
			}
		cal.uptimes = uptimes.length;
		cal.downtimes = new Date().getDate()  - cal.uptimes
		let sum = 0;
		for(let k in  listByDay){
			if(listByDay[k].length == 2){
				let time = listByDay[k][1].get('time') - listByDay[k][0].get('time')
				sum = sum + time
			}
		}
		// console.log(sum)
		let hours = mssToHours(sum)
		cal.uphours= hours[0]
		cal.calIncome= hours[1]
		cal.month = getMonthTime()
		//cal.calIncome cal.calIncome * 分钟收入
		
		//uptimes
		//action == true 
		//先这样存 决断下月的有没有，没有就新建一份
		let newReport = new Report()
		Parse.Object.registerSubclass('Report', Report);

		let reports = new Parse.Query(newReport);
		reports.equalTo("parent", Parse.User.current());
		reports.equalTo("month", cal.month);
		reports.find().then(function(report){
			console.log('get report list')
			console.log(report)
			if(report.length){
				newReport = report[0]
			}

			newReport.spawn({...cal})
			newReport.save().then()
		})
		

	}

	).catch(function(error) {
		// There was an error.
		alert(error.message)
	});
  
	
	console.log(cal)
}

//获得本月的开端日期时间
function getMonthStartDate(){
	var now = new Date(); //当前日期
// var nowDayOfWeek = now.getDay(); //今天本周的第几天
// var nowDay = now.getDate(); //当前日
var nowMonth = now.getMonth(); //当前月
var nowYear = now.getYear(); //当前年

var monthStartDate = new Date(nowYear, nowMonth, 1);
return monthStartDate;
}
function getMonthTime(lastMonth){
	var now = new Date(); //当前日期
// var nowDayOfWeek = now.getDay(); //今天本周的第几天
// var nowDay = now.getDate(); //当前日
var nowMonth = now.getMonth(); //当前月
if(!lastMonth){
	nowMonth = nowMonth + 1
}
var nowYear = now.getFullYear(); //当前年
if(lastMonth && nowMonth == 0){
	nowYear = nowYear - 1
	nowMonth = 12
}
return nowYear + "-" + nowMonth;
}
function mssToHours(mss){
	var hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
	var minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
	return [hours + "小时" + minutes + "分钟",hours*60+minutes]
}
