import Parse from 'parse';
//打卡记录
export  class Record extends Parse.Object {
    constructor() {
        // Pass the ClassName to the Parse.Object constructor
        super('Record');
        // All other initialization
        this.user = '';
        //收入 --时间段
        this.action = true; //'0|1 1上0下 up上其它下 true上false下'
        this.timeString = ''
    }
    spawn({action,timeString}) {
        this.set('parent',Parse.User.current())
        this.set('action',action)
        this.set('timeString',timeString)
        this.set('time',new Date())
        this.set('day',new Date().getDate() + "")
    }
}


